"use client"

import { useRef, useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Input } from "@/components/ui/input"
import { Loader2, Send, Info, Home, FileText, Settings, Menu } from "lucide-react"
import Image from "next/image"
import { SourceDocument, SourceDocuments } from "@/components/source-documents"
import { useMobile } from "@/hooks/use-mobile"
import ReactMarkdown from "react-markdown"
import remarkGfm from "remark-gfm"
import { ThemeToggle } from "@/components/theme-toggle"

// Define a custom message type that includes an 'id' and optional sources
interface ChatMessageType {
  id: string;
  role: 'user' | 'assistant' | 'system';
  content: string;
  sources?: SourceDocument[];
}

export default function ChatPage() {
  const [messages, setMessages] = useState<ChatMessageType[]>([])
  const [input, setInput] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<Error | null>(null)

  const messagesEndRef = useRef<HTMLDivElement>(null)
  const isMobile = useMobile()
  const [showSidebar, setShowSidebar] = useState(!isMobile)
  const [activeSection, setActiveSection] = useState("home")
  const [userMessageCount, setUserMessageCount] = useState(0); // New state to trigger scroll

  useEffect(() => {
    setShowSidebar(!isMobile)
  }, [isMobile])

  // New useEffect for scrolling only when user sends a message
  useEffect(() => {
    if (userMessageCount > 0 && messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
      // console.log("User message sent, scrolling to end.", messagesEndRef.current);
    }
  }, [userMessageCount]);

  useEffect(() => {
    if (error) {
      // console.error("Chat error object:", error) // Changed from useChat error
    }
  }, [error])

  const showWelcomeMessage = messages.length === 0 && !isLoading && !error

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    if (!input.trim() || isLoading) return

    const userMessage: ChatMessageType = {
      id: Date.now().toString(),
      role: 'user',
      content: input,
    }

    setMessages((prevMessages) => [...prevMessages, userMessage])
    setInput('')
    setIsLoading(true)
    setError(null)
    setUserMessageCount(prevCount => prevCount + 1); // Increment to trigger scroll

    const newAssistantMessageId = `ai-response-${Date.now()}`

    try {
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ messages: [userMessage] }),
      })

      if (!response.ok || !response.body) {
        const errorText = await response.text()
        throw new Error(`API request failed with status ${response.status}: ${errorText}`)
      }

      const reader = response.body.getReader()
      const decoder = new TextDecoder()
      let assistantResponseContent = ''
      let assistantSources: SourceDocument[] = [];

      while (true) {
        const { value, done } = await reader.read()
        if (done) break

        const chunk = decoder.decode(value, { stream: true })
        // console.log('Frontend: Raw chunk received:', chunk.length, 'bytes')
        const sseMessages = chunk.split('\n\n').filter(msg => msg.startsWith('data: ')).map(msg => msg.substring(6))

        for (const sseMessage of sseMessages) {
          // console.log('Frontend: Processing SSE message:', sseMessage)
          try {
            const parsedData = JSON.parse(sseMessage)
            // console.log('Frontend: Parsed SSE data:', parsedData)
            
            if (parsedData.type === 'text') {
              const content = parsedData.content;
              if (content) {
                assistantResponseContent += content;
              }
            } else if (parsedData.type === 'sources') {
              if (parsedData.sources) {
                assistantSources = parsedData.sources;
              }
            }

            setMessages((prevMessages) => {
              const existingAssistantMessageIndex = prevMessages.findIndex(
                (msg) => msg.id === newAssistantMessageId
              );

              if (existingAssistantMessageIndex !== -1) {
                // Update existing assistant message
                return prevMessages.map((msg, index) =>
                  index === existingAssistantMessageIndex
                    ? { ...msg, content: assistantResponseContent, sources: assistantSources }
                    : msg
                );
              } else {
                // Add new assistant message (first chunk)
                return [
                  ...prevMessages,
                  {
                    id: newAssistantMessageId,
                    role: 'assistant',
                    content: assistantResponseContent,
                    sources: assistantSources
                  },
                ];
              }
            });
          } catch (jsonError) {
            console.error('Failed to parse SSE message:', sseMessage, jsonError)
          }
        }
      }
    } catch (err) {
      console.error('Error during streaming:', err)
      setError(err instanceof Error ? err : new Error('An unknown error occurred.'))
      setMessages((prevMessages) => [
        ...prevMessages,
        {
          id: Date.now().toString(),
          role: 'assistant',
          content: 'I apologize, but I encountered an error while processing your request. Please try again.',
        },
      ])
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="h-screen w-screen bg-gray-50 dark:bg-gray-900 overflow-hidden">
      {/* Mobile Menu Button */}
      <button
        onClick={() => setShowSidebar(!showSidebar)}
        className="fixed top-4 left-4 z-50 p-2 rounded-lg bg-blue-800 text-white lg:hidden hover:bg-blue-700 transition-colors"
        aria-label="Toggle menu"
      >
        <Menu className="h-6 w-6" />
      </button>
      <div className="flex h-full w-full">
        {/* Sidebar */}
        <div
          className={`fixed lg:static top-0 left-0 h-full z-40 transform transition-transform duration-300 ease-in-out ${
            showSidebar ? "translate-x-0" : "-translate-x-full"
          } lg:translate-x-0 w-64 bg-blue-800 text-white shadow-lg`}
        >
          <div className="p-4 h-full flex flex-col">
            <div className="flex flex-col items-center justify-center mb-8">
              <div className="flex items-center mb-2">
                <div className="bg-white p-2 rounded-xl mr-2 shadow-md hover:shadow-lg transition-shadow">
                  <Image
                    src="/mohua-logo-removebgpng.png"
                    alt="Government of India Emblem"
                    width={60}
                    height={60}
                    className="object-contain"
                  />
                </div>
                <div className="bg-white p-2 rounded-xl shadow-md hover:shadow-lg transition-shadow">
                  <Image
                    src="/pmay-logo-removebg-preview.png"
                    alt="PMAY Logo"
                    width={80}
                    height={80}
                    className="object-contain"
                  />
                </div>
              </div>
              <h1 className="text-xl font-bold tracking-tight">PMAY Chatbot</h1>
            </div>

            <nav className="space-y-2 mb-6">
              <Button
                variant="ghost"
                className={`w-full justify-start text-white hover:bg-blue-700/80 transition-colors ${
                  activeSection === "home" ? "bg-blue-700" : ""
                }`}
                onClick={() => {
                  setActiveSection("home")
                  if (isMobile) setShowSidebar(false)
                }}
              >
                <Home className="mr-2 h-5 w-5" />
                Home
              </Button>
              <Button
                variant="ghost"
                className={`w-full justify-start text-white hover:bg-blue-700/80 transition-colors ${
                  activeSection === "documents" ? "bg-blue-700" : ""
                }`}
                onClick={() => {
                  setActiveSection("documents")
                  if (isMobile) setShowSidebar(false)
                }}
              >
                <FileText className="mr-2 h-5 w-5" />
                Documents
              </Button>
              <Button
                variant="ghost"
                className={`w-full justify-start text-white hover:bg-blue-700/80 transition-colors ${
                  activeSection === "settings" ? "bg-blue-700" : ""
                }`}
                onClick={() => {
                  setActiveSection("settings")
                  if (isMobile) setShowSidebar(false)
                }}
              >
                <Settings className="mr-2 h-5 w-5" />
                Settings
              </Button>
            </nav>

            {/* Dynamic Content Based on Active Section */}
            <div className="flex-1 overflow-y-auto">
              {activeSection === "home" && (
                <div className="space-y-4">
                  <div className="text-sm text-blue-200">
                    <h3 className="font-semibold mb-2 text-lg">Quick Actions</h3>
                    <ul className="space-y-2 text-sm">
                      <li className="flex items-center hover:text-white transition-colors cursor-pointer">
                        <span className="mr-2">•</span>
                        Ask about PMAY eligibility
                      </li>
                      <li className="flex items-center hover:text-white transition-colors cursor-pointer">
                        <span className="mr-2">•</span>
                        Check application status
                      </li>
                      <li className="flex items-center hover:text-white transition-colors cursor-pointer">
                        <span className="mr-2">•</span>
                        Learn about benefits
                      </li>
                      <li className="flex items-center hover:text-white transition-colors cursor-pointer">
                        <span className="mr-2">•</span>
                        Find nearest office
                      </li>
                    </ul>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
        {/* Main Content */}
        <div className={`flex-1 flex flex-col h-full transition-all duration-300 ${
          showSidebar ? "lg:ml-64" : "ml-0"
        } ml-0 lg:ml-64`}> {/* Always offset on desktop */}
          {/* Header */}
          <header className="bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700 p-4 flex items-center justify-between shadow-sm shrink-0">
            <div className="flex items-center space-x-4">
              <h2 className="text-xl font-semibold text-gray-800 dark:text-white">
                {activeSection === "home" ? "Chat" : activeSection.charAt(0).toUpperCase() + activeSection.slice(1)}
              </h2>
            </div>
            <div className="flex items-center space-x-4">
              <ThemeToggle />
              <Button
                variant="ghost"
                size="icon"
                className="text-gray-600 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700"
                onClick={() => {
                  // Add info button functionality here
                }}
              >
                <Info className="h-5 w-5" />
              </Button>
            </div>
          </header>

          {/* Chat Area */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4">
            {showWelcomeMessage && (
              <div className="flex flex-col items-center justify-center h-full text-center p-4">
                <div className="max-w-md space-y-4">
                  <h2 className="text-2xl font-bold text-gray-800 dark:text-white">
                    Welcome to PMAY Chatbot
                  </h2>
                  <p className="text-gray-600 dark:text-gray-300">
                    Ask me anything about the Pradhan Mantri Awas Yojana (PMAY) scheme.
                  </p>
                </div>
              </div>
            )}

            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${
                  message.role === "user" ? "justify-end" : "justify-start"
                }`}
              >
                <div
                  className={`max-w-[80%] rounded-lg p-4 ${
                    message.role === "user"
                      ? "bg-blue-600 text-white"
                      : "bg-white dark:bg-gray-800 text-gray-800 dark:text-white shadow-md"
                  }`}
                >
                  <div className="flex items-start space-x-3">
                    {message.role === "assistant" && (
                      <Avatar className="h-8 w-8">
                        <AvatarFallback className="bg-blue-100 text-blue-800">
                          AI
                        </AvatarFallback>
                      </Avatar>
                    )}
                    <div className="flex-1 space-y-2">
                      <div className="prose dark:prose-invert max-w-none">
                        <ReactMarkdown remarkPlugins={[remarkGfm]}>
                          {message.content}
                        </ReactMarkdown>
                      </div>
                      {message.sources && message.sources.length > 0 && (
                        <SourceDocuments documents={message.sources} />
                      )}
                    </div>
                    {message.role === "user" && (
                      <Avatar className="h-8 w-8">
                        <AvatarFallback className="bg-blue-100 text-blue-800">
                          U
                        </AvatarFallback>
                      </Avatar>
                    )}
                  </div>
                </div>
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>

          {/* Input Area */}
          <div className="border-t border-gray-200 dark:border-gray-700 p-4 bg-white dark:bg-gray-800">
            <form onSubmit={handleSubmit} className="flex space-x-4">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                placeholder="Type your message..."
                className="flex-1 rounded-lg border-gray-300 dark:border-gray-600 focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-white"
                disabled={isLoading}
              />
              <Button
                type="submit"
                disabled={isLoading || !input.trim()}
                className="bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors"
              >
                {isLoading ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <Send className="h-5 w-5" />
                )}
              </Button>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}
